* {
  font-family: "Quicksand", Arial, sans-serif;
}

.my-main-animation {
  display: flex;
}

.my-main-animation .my-main-text {
  color: #111;
  font-size: 3rem;
  font-weight: 400;
  margin: 2rem;
  margin-left: 3.5rem;
  color: aliceblue;
}

.my-main-option {
  height: 5rem;
  line-height: 5rem;
  overflow: hidden;
}

.my-main-option li {
  list-style: none;
  color: #face30;
  font-size: 3rem;
  position: relative;
  top: 0;
  -webkit-animation: slide 12s steps(4) infinite;
          animation: slide 12s steps(4) infinite;
}
@-webkit-keyframes slide {
  100% {
    top: -360px;
  }
}
@keyframes slide {
  100% {
    top: -360px;
  }
}
.my-main-option li span {
  position: relative;
  margin: 2px 0;
  line-height: 90px;
}

.my-main-option li span::after {
  content: " ";
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(4, 12, 24, 1) 0%;
  border-left: 2px solid #face30;
  -webkit-animation: typing 3s steps(14) infinite;
          animation: typing 3s steps(14) infinite;
  margin-top: 1.5rem;
}
@-webkit-keyframes typing {
  40%,
  60% {
    left: calc(100% + 30px);
  }
  100% {
    left: 0;
  }
}
@keyframes typing {
  40%,
  60% {
    left: calc(100% + 30px);
  }
  100% {
    left: 0;
  }
}

.whoo {
  margin-top: 3rem;
  margin-left: 3.7rem;
  color: aliceblue;
}
@media (max-width: 800px) {
  .my-main-animation .my-main-text {
    font-size: 3rem;
  }
}

@media (max-width: 500px) {
  .my-main-animation .my-main-text {
    font-size: 1.5rem;
  }
}

