.box {
  margin: 15px;
  margin-top: 50px;
}
.head {
  display: inline-block;
  padding: 0px 12px;
  color: rgb(252, 252, 252);
  margin-bottom: 20px;
  margin-left: 30px;
  letter-spacing: 5px;
  font-size: 11px;
}

.container {
  width: 100%;
  margin-left: 30px;
  display: block;
  margin-top: 30px;
  border-radius: 3px;
  padding: 0 42px 0 10px;
  margin-left: 30px;
  /* background-color: red; */
}
.row:before {
  content: " ";
  display: table;
}
.row:after {
  clear: both;
}
.row_md_12 {
  display: flex;
  flex-direction: column;
}
.timeline_centered {
  z-index: 1;
  position: relative;
  margin-bottom: 30px;
}
.timeline_centered:before {
  content: "";
  position: absolute;
  display: block;
  width: 4px;
  background: #f3f2f7;
  top: 20px;
  z-index: -1;
  bottom: 20px;
  margin-left: 19px;
}
.timeline_entry {
  margin-bottom: 10px;
  clear: both;
}
.timeline_icon {
  display: block;
  width: 40px;
  font-size: 20px;
  height: 40px;
  background-color: #f9bf3f;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  border-radius: 50%;
  text-align: center;
  -moz-box-shadow: 0 0 0 5px #f2f3f7;
  -webkit-box-shadow: 0 0 0 5px #f2f3f7;
  box-shadow: 0 0 0 5px #f2f3f7;
  line-height: 35px;
  float: left;
}
.timeline_icon_2 {
  background: #de1db4;
}
.timeline_icon_5 {
  background: #00ffff;
}

.timeline_icon_4 {
  background: #ff0040;
}

.timeline_entry_inner {
  position: relative;
  top: 0px;
  margin-left: 11.5px;
}
.timeline_icon_3 {
  display: block;
  width: 20px;
  font-size: 20px;
  height: 20px;
  background: #fff;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  border-radius: 50%;
  text-align: center;
  -moz-box-shadow: 0 0 0 5px #f2f3f7;
  -webkit-box-shadow: 0 0 0 5px #f2f3f7;
  box-shadow: 0 0 0 5px #f2f3f7;
  line-height: 35px;
  float: left;
}

