.label {
  position: relative;
  background: #010713;
  padding: 1.5em;
  margin-left: 60px;
}
.label h2 {
  font-size: 20px;
  font-family: "Quicksand", Arial, sans-serif;
  font-weight: 500;
  margin: 0 0 30px 0;
  color: rgb(255, 255, 255);
}
.label h2 span {
  -webkit-opacity: 0.4;
  -moz-opacity: 0.4;
  opacity: 0.4;
  -ms-filter: alpha(opacity=40);
  filter: alpha(opacity=40);
  font-size: 16px;
}

.label p {
  margin-bottom: 1.5em;

  /* margin-left: 200px; */
}
.label {
  color: #00ffff;
}
p > a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  color: rgb(121, 189, 252);
  text-decoration: none;
}

@keyframes text {
  from {
    transform: translateX(-30px);
    opacity: 0.1;
  }
  /* to{opacity: 1;} */
}

@media (max-width: 992px) {
  .box {
    text-align: center;
  }
}

@media (max-width: 440px) {
  .container {
    padding: 0 42px 0 0;
  }
}
